.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.test {
  display: flex;
  flex-direction: row;
  width: 40em;
  margin: 5em;
}

.numBox {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 2em;
}

.numContainers {
  width: 8em;
  height: 6em;
}
.numContainers span {
  color: #ffffff;
  font-weight: bold;
  font-size: 1.4em;
}

.buttons {
  display: flex;
  flex-direction: row;
  margin: 2em 1em;
  align-items: flex-start;
}

.buttons button {
  height: 2em;
  margin: 0.5em;
  background: rgb(114, 195, 220);
    color: #fff;
    border: none;
    border-radius: 5px;
    border-bottom: 2px solid rgb(47, 69, 78);
}

@media screen and (max-width: 480px) {

  .numContainers {
    width: 90%;
    height: 3em;
  }
  .test {
    flex-direction: column;
}
  .numBox {
    flex-direction: column;
  }
  .numContainers span {
    text-align: center;
    display: flex;
    justify-content: center;
  }
}
